.product {
    height: 508px;
    width: 300px;
    border: 1px solid #95A0A7;
    border-radius: 8px;
    position: relative;

}

.product img {
    width: 286px;
    height: 286px;
    border-radius: 8px;
    margin: 7px;
}

.product-info {
    margin: 0px 14px;
}

.product-info p {
    margin-bottom: 0;
    margin-top: 5px;
}

.product-name {
    font-family: 'Lato';
    font-style: normal;
    font-weight: 400;
    font-size: 21px;
    margin-top: 7px;

}

.btn-cart {
    width: 100%;
    background-color: #FFE0B3;
    border: 0;
    border-radius: 0 0 8px 8px;
    position: absolute;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.btn-cart:hover {
    background-color: orange;
    color: white;
    cursor: pointer;
}

.btn-cart .btn-text {
    margin-right: 5px;
}